.body-container{
    background-image: url(../asssests/background.gif);
    background-repeat: no-repeat;
    background-size: cover;
    width: 100%;
    height: 100%;
    z-index: -1;
    position: absolute;
}
.zomato-logo{
    width: 200px;
    border-radius: 50%;
    margin-bottom: 20px;
}
.flex-box{
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
}
.login-container{
    position: absolute;
    left: 0;
    right: 0;
    top: 0;
    bottom: 0;
}
.login-box{
    width: 500px;
    height: 540px;
}
.input-box{
    display: flex;
    flex-direction: column;
    margin-bottom: 0.5rem;
}
.input-box input{
    margin-bottom: 0.2rem;
    padding: 10px;
    font-weight: 600;
    font-size: 20px;
    outline: none;
    background: transparent;
    color: white;
    width: 250px;
    border: transparent;
    border-bottom-style: 1px solid white;
    border-bottom: 1px solid white;
    border-bottom-width: 2px;
}
.input-box input:focus{
    border: transparent;
}
.input-box p{
    color: #6D747A;
    font-weight: 600;
    font-size: 18px;
    margin: 0px;
    margin-top: 10px;
}
.button{
    border: 1px solid #979fa7;
    border-radius: 5px;
    color: white;
    padding: 14px;
    text-decoration: unset;
    margin-top: 15px;
}
.button:hover{
    cursor: pointer;
    transform: scale(1.1);
}